module.exports = "<div class=\"field\" [ngClass]=\"{ error: (!control.valid && control.dirty) }\">\n  <div class=\"ui input {{ innerClass }}\"\n       [ngClass]=\"{'icon': icon, 'error': (!control.valid && control.dirty)}\">\n    <label *ngIf=\"label\" class=\"ui label\">{{ label }}</label>\n    <input [type]=\"type\" [ngModel]=\"value\" (ngModelChange)=\"setValue($event)\" (focus)=\"this.control.markAsTouched()\"\n           (input)=\"this.control.markAsDirty()\" placeholder=\"{{ placeholder }}\">\n    <div class=\"ui pointing below red basic label overlay\" *ngIf=\"errorLabels && !control.valid\">\n      <span *ngFor=\"let error of activeErrorLabels\">{{ errorLabels[error] }}</span>\n    </div>\n    <i *ngIf=\"icon\" class=\"{{ icon }} icon\"></i>\n  </div>\n</div>\n";

